@import url(//fonts.googleapis.com/css?family=Open+Sans:400italic,600italic,600,400,300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Chart {
  position: relative;
  flex-grow: 1;
  min-height: 250px;
}

.Chart-canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.Chart-canvas-scale {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.ChartSidebar {
  padding: 20px;
  min-width: 20%;
  font-size: 15px;
  margin: 20px;
}

@media (max-width: 640px) {
  .ChartSidebar {
    margin: 10px;
  }
}


.ChartSidebar-light {
  border: solid 1px #f4f4f4;
}

.ChartSidebar-dark {
  background: #000;
  color: #ccc;
  border: solid 1px #222;
}

.ChartSidebar h3 {
  font-weight: normal;
  font-size: 16px;
  margin: 5px 0;
}

.ChartSidebar h2 {
  font-weight: lighter;
  font-size: 20px;
  margin: 25px 0 10px 0;
}

.ChartSidebar-control {
  margin-bottom: 10px;
}

.ChartSidebar-control label {
  font-size: 14px;
  font-weight: lighter;
  display: block;
}

.ChartSidebar-control input[type=text] {
  display: block;
  padding: 2px 8px;
  font-size: 14px;
  width: 50px;
  margin-left: 5px;
}

.ChartSidebar-control-dark input[type=text] {
  color: #ddd;
  background: #000;
  border: 1px solid #444;
}

.ChartSidebar-control input[type=radio] {
  display: inline;
  margin-right: 5px;
}


.ChartSidebar-name {
  font-size: 30px;
  font-weight: lighter;
}

.ChartSidebar-name-light {
  color: #888;
}

.ChartSidebar-name-dark {
  color: #888;
}

.ChartSidebar-price {
  margin: 0;
}

.ChartSidebar-change {
  font-size: 30px;
}

.ChartSidebar-percent {
  font-size: 30px;
  font-weight: bold;
}

.ChartSidebar-bull {
  color: #008800;
}

.ChartSidebar-bear {
  color: #dd0000;
}

.ChartSidebar-table {
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 14px;
}

.ChartSidebar-table th, .ChartSidebar-table td {
  padding: 4px 10px;
}

.ChartSidebar-table-dark th, .ChartSidebar-table-dark td {
  border-bottom: 1px solid #444;
}

.ChartSidebar-table-light th, .ChartSidebar-table-light td {
  border-bottom: 1px solid #eee;
}

.ChartSidebar-table th {
  color: #888;
  text-align: left;
  font-weight: normal;
  padding-left: 0;
}

.ChartSidebar-tips {
  font-weight: lighter;
  margin: 0;
}

.ChartSidebar-gihub {
  display: flex;
  flex-direction: column;
}

.ChartSidebar-gihub svg {
  margin-right: 5px;
}

.ChartSidebar-gihub a {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #666;
  text-decoration: none;
  margin-bottom: 5px;
}

.ChartViewAdvanced-container {
  flex-grow: 1;
  display: flex;
  margin: 10px;
  min-height: 360px;
}

@media (max-width: 640px) {
  .ChartViewAdvanced-container {
    margin: 0;
  }
}

.Chart {
  position: relative;
  flex-grow: 1;
  min-height: 250px;
}

.Chart-canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.Chart-canvas-scale {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.ChartBasicView {
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-grow: 1;
}

.ChartBasicView-container {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  font-family: "Open Sans";
  min-height: 100vh;
}

.App-dark {
  background: #000;
}

.App-header {
  display: flex;
  align-items: center;

  margin: 0;
  padding: 10px 15px;
  height: 30px;
  z-index: 10;
}

@media (max-width: 640px) {
  .App-header {
    padding: 10px 0;
  }
}

@media (max-width: 640px) {
  .App-header {
    height: 24px;
  }
}

.App-header-dark a {
  color: #aaa;
}

.App-header-light a {
  color: #000;
}

.App-header a {
  z-index: 10;
  display: block;
  font-size: 12px;
  font-weight: normal;
  text-decoration: none;
  margin-left: 20px;
  padding: 5px 5px 7px 5px;
}


.App-header-links {
  display: flex;
  align-items: center;
}

@media (max-width: 640px) {
  .App-header-links {
    display: none;
  }
}

.App-header button {
  font-size: 12px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #fff;
  background: none;
  cursor: pointer;
  margin-left: 20px;
  padding: 5px;
}

.App-header button.active {
  border-bottom: 2px solid #fc0;
}

.App-header-dark button.active {
  border-bottom: 2px solid #860;
}

.App-header-dark button {
  color: #aaa;
  border-bottom: 2px solid #000;
}

.App-header-light {
  background: #fff;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.04);
  border-bottom: 1px solid #f4f4f4;
}

.App-header-dark {
  background: #000;
  border-bottom: 1px solid #222;
  color: #aaa;
}

.App-content {
  padding: 0;
  flex-grow: 1;
  display: flex;
  flex-direction: row;
}

@media (max-width: 960px) {
  .App-content {
    flex-direction: column;
  }
}

.App-header-home {
  font-size: 22px;
  font-weight: lighter;
  margin: 0 10px;
}

.App-header-menu {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 55px;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media (min-width: 640px) {
  .App-header-menu {
    display: none;
  }

  .App-header-menu-toggle {
    display: none;
  }
}

.App-header-menu-light {
  background: #fff;
}

.App-header-menu-dark {
  background: #000;
}

